<template>
    <div class="navigation-search">
        <div class="search-wrapper paper shadow">
            <div class="back" @click.stop="handlerBackNavigation">
                <mu-icon value="navigate_before" size="30"></mu-icon>
            </div>
            <label class="input-wrapper">
                <mu-text-field v-model="params.name" type="search" placeholder="请输入关键字搜索"
                               @keyup.enter="searchLocationInfo" ref="locationInput"
                               solo/>
            </label>
            <mu-button style="height: 100%" @click="searchLocationInfo" flat>搜索</mu-button>
        </div>
        <div class="tool paper shadow">
            <div class="item" @click="handlerMyLocation">
                <mu-icon value="place" color="#0DEC42" size="17"/>
                <span>我的位置</span>
            </div>
            <div class="item" @click="handlerCollection">
                <mu-icon value="star" color="#e91e63" size="17"/>
                <span>我的收藏</span>
            </div>
            <div class="item" @click="handlerSelection">
                <mu-icon value="pin_drop" color="#4fc3f7" size="17"/>
                <span>地图选点</span>
            </div>
        </div>
        <div class="scroll-wrapper shadow">
            <mu-load-more class="list paper" @load="load" v-if="list.length">
                <mu-list class="pn">
                    <mu-list-item class="border-1px-b" v-for="(item, index) in list" :key="item.id"
                                  @click.native="addRoute(item)">
                        <mu-list-item-action>
                            <mu-icon color="grey" value="place"/>
                        </mu-list-item-action>
                        <mu-list-item-content>
                            <mu-list-item-title>{{item.name}}</mu-list-item-title>
                            <mu-list-item-sub-title v-if="item.distance">距离：{{item.distance}}公里</mu-list-item-sub-title>
                        </mu-list-item-content>
                        <mu-list-item-action>
                            <mu-icon color="grey" value="reply"/>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </mu-load-more>
            <div class="historical paper" v-else>
                <div class="historical-item border-1px-b"
                     v-for="item in searchHistory"
                     v-if="searchHistory.length">
                    <div class="text" @click="handlerHistory(item)">{{item}}</div>
                    <mu-icon value="close" size="16" color="grey" @click.stop="remove(item)"/>
                </div>
                <div class="border-1px-b" v-if="!searchHistory.length">
                    <div style="width: 100%; height: 46px; line-height: 46px; text-align: center; font-size: 14px">
                        暂时没有历史纪录
                    </div>
                </div>
                <div>
                    <mu-button style="width: 100%; height: 46px" @click="clear" flat>清空历史纪录</mu-button>
                </div>
            </div>
        </div>
        <transition name="views-left">
            <keep-alive>
                <collection v-model="visible" v-if="visible" @change="addRoute"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Collection from '../about/collection/collection'
    import {MapMixins} from "../../common/js/mixins/index"
    import {getNavigationals} from "../../api/navigation"
    import {isArray} from "../../types/index"
    import {routeMixins, appMixins} from "../../common/mixins"

    export default {
        name: "navigation-search",
        mixins: [appMixins, MapMixins, routeMixins],
        data() {
            return {
                visible: false,
                params: {
                    types: [],
                    name: '',
                    pageNumber: 1,
                    pageSize: 30
                },
                list: []
            }
        },
        activated() {
            if (this.$route.name === 'route-sn') {
                this.params.name = this.sn.name
            }
            if (this.$route.name === 'route-en') {
                this.params.name = this.en.name
            }

            this.list = []

            if (this.params.name) {
                this.getNavigationList()
            }

            const input = this.$refs.locationInput.$el.querySelector('input')

            setTimeout(() => {
                input.focus()
                input.select()
            }, 200)
        },
        deactivated() {
            this.visible = false
        },
        methods: {
            // 退出导航位置搜索
            handlerBackNavigation() {
                this.$router.back()
            },
            // 搜索
            getNavigationList() {
                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在查询，请稍等 ...',
                    duration: 0
                })

                this.params.pageNumber = 1

                getNavigationals(this.params).then(res => {
                    this.list = isArray(res)

                    if (this.list.length < 1) {
                        this.$toast('没有搜索到结果')
                    } else {
                        this.$toast.clear()
                    }

                    this.saveRouteSearchHistory(this.params.name)
                }).catch(err => {
                    console.log(err)

                    this.$toast.clear()
                })
            },
            load() {
                this.params.pageNumber++

                getNavigationals(this.params).then(res => {
                    this.list.push(...isArray(res))
                })
            },
            // 点击搜索
            searchLocationInfo() {
                if (!this.params.name) {
                    this.list = []

                    return this.$toast('请输入关键词')
                }

                this.getNavigationList()
            },
            // 点击添加位置信息
            addRoute(item) {
                if (this.$route.name === 'route-sn') {
                    this.setRouteSn(item)
                }
                if (this.$route.name === 'route-en') {
                    this.setRouteEn(item)
                }

                this.$router.back()
            },
            // 点击我的位置
            handlerMyLocation() {
                if (!this.gps) {
                    return this.$toast('定位服务未开启！')
                }

                this.addRoute({
                    name: '我的位置'
                })
            },
            // 点击我的收藏
            handlerCollection() {
                this.visible = true
            },
            // 点击历史纪录
            handlerHistory(item) {
                this.params.name = item

                this.getNavigationList()
            },
            remove(item) {
                this.deleteRouteSearchHistory(item)
            },
            clear() {
                this.clearRouteSearchHistory()
            },
            handlerSelection() {
                if (this.$route.name === 'route-sn') {
                    this.$router.push({
                        name: 'selection-sn'
                    })
                }
                if (this.$route.name === 'route-en') {
                    this.$router.push({
                        name: 'selection-en'
                    })
                }
            }
        },
        components: {
            Collection
        }
    }
</script>

<style lang="stylus">
    @import "../../common/stylus/variable.styl"

    /* search navigation location info */
    .navigation-search
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 200
        display flex
        flex-direction column
        overflow hidden

        .search-wrapper
            display flex
            justify-content center
            align-items center
            padding-left 10px
            width 100%
            height 50px
            overflow hidden

            .input-wrapper
                flex 1

                input
                    display block
                    height 30px
                    line-height 30px
                    width 100%
                    font-size $font-size-ms
                    text-indent .5em
                    outline none

        .tool
            position relative
            z-index 1
            display flex
            align-items center
            margin 15px 0
            width 100%
            height 50px

            .item
                flex 1
                display flex
                justify-content center
                align-items center
                height 30px
                border-right 1px dashed #E7E7E8

                &:last-child
                    border-right-color transparent

                > i
                    margin-right 5px

                > span
                    font-size $font-size-mm

        .scroll-wrapper
            flex 1
            width 100%
            overflow-x hidden
            overflow-y auto

            .list
                box-shadow 0 0 7px -2px rgba(188, 188, 188, .33)

                .item
                    position relative
                    display flex
                    align-items center
                    width 100%
                    height 70px
                    overflow hidden

                    > i
                        padding 20px

                    .info
                        flex 1
                        display flex
                        align-items center
                        height 100%

                        .text > div
                            margin-bottom 10px

                            &:last-child
                                margin-bottom 0

                        .name
                            font-size $font-size-ml

                        .distance
                            font-size $font-size-sm

                        .text
                            flex 1
                            display flex
                            flex-direction column
                            justify-content center

                        > i
                            padding 16px

                    &:last-child .info
                        border-color transparent

            .historical
                margin 20px 10px
                box-shadow 0 0 7px -2px rgba(188, 188, 188, .33)

                .historical-item
                    display flex
                    align-items center
                    height 44px
                    padding 0 15px

                    .text
                        flex 1
                        font-size $font-size-ms

    /* search navigation location info */
</style>



